.storagebox-container {
  display: flex;
  width: 100vw;
  justify-content: center;
}
.storagebox {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.storagebox-title {
  font-family: "Fira Sans Extra Condensed", sans-serif;
  background-color: #d9d9d9;
  padding: 1rem 7rem;
  border-radius: 35px;
  border: 1px solid greenyellow;
  font-size: 22px;
}

.grocerylist-content {
  border: greenyellow 1px solid;
  height: 30rem;
  background-color: #d9d9d9;
  border-radius: 15px;
  width: 50rem;
  margin: 2.5rem;
  max-width: 33vw;
}